body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100vw;
  height: 100vh;
}

#root{
	width: inherit;
	height: inherit;
	background-color: #2897B7;
}

.game{
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row-reverse;
	    flex-direction: row-reverse;

}

.game-board{
	padding: 2.5%;
	width: 75%;
	height: 75%;

}

.game-info{
	padding: 2.5%;
	width: 20%;
	height: 75%;
	overflow: scroll;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	text-align: center;

}

.game-info ol li button{
	width: 100%;
	color: #f8f8f8;
	background-color: black;
	border: transparent;
	padding: 1%;
	margin: 1% 0;

	-webkit-transition-property: border;

	-o-transition-property: border;

	transition-property: border;
	-webkit-transition-duration: 0.5s;
	     -o-transition-duration: 0.5s;
	        transition-duration: 0.5s;
}

.game-info .highlighted{
	border: solid #FF4848;
}

.board{
	width: 100%;
	height: 100%;
	margin: 2.5% 0;
}

.board-row{
	width: 100%;
	height: 12.5%;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-align: start;
	    align-items: flex-start;
}

.square{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	width: 12.5%;
	height: 100%;
	padding: 0;
	border: white solid 1px;
	color: white;

}

.R{
	background: #F4A460;
}

.B{
	background: black;
}

.piece-value{
	display: none;
}

.piece{
	font-size: 30px;
}

.black-piece{
	color: black;
}

.red-piece{
	color: red;
}

@media only screen and (max-width: 767px){
	.game{
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: distribute;
		    justify-content: space-around;

	}

	.game-board{
		padding: 2.5%;
		width: 85%;
		height: 40%;

	}

	.game-info{
		width: 85%;
		height: 40%;

		-ms-flex-align: center;

		    align-items: center;
	}

	.game-info ol{
		width: 50%;
	}

	.game-info button{
		width: 100%;
		padding: 5% 0;

	}

	.piece{
		font-size: 20px;
	}
}




